<template><section><h1>LabelCollection</h1>
<p>The <code v-pre>vc-collection-primitive-label</code> component is used to load a renderable collection of labels. Labels are viewport-aligned text positioned in the 3D scene. Each label can have a different font, color, scale, etc.</p>
<h2>Example</h2>
<h3>Load LabelCollection with <code v-pre>vc-collection-primitive-label</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-label :labels="labels"></vc-collection-primitive-label>
        <vc-collection-primitive-label>
          <template v-for="(polyline, index) of polylines">
            <vc-primitive-label :position="polyline.positions[polyline.positions.length-1]" :key="'label'+index" :text="'Area'+(polyline.area > 1000000 ? (polyline.area / 1000000).toFixed(2) + 'km²' : polyline.area.toFixed(2) + '㎡')" showBackground :horizontalOrigin="1">
            </vc-primitive-label>
          </template>
        </vc-collection-primitive-label>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
